
<template>
  <div>
    <h2>Login11</h2>
    <form @submit.prevent="signin(email, password)">
      <input type="text" v-model="email" placeholder="Email Address" />
      <input type="password" v-model="password" placeholder="Password" />
      <input type="submit" value="Login" />
    </form>
    <p>
      <i></i>
    </p>
  </div>
</template>

<script>
import cookie from "vue-cookies";

export default {
  name: "Signin",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      uid: "",
      password: ""
    };
  },
  methods: {
    signin(email, password) {
      const uid = email;
      //const password = password;

      if (!uid || !password) {
        return false;
      }

      //console.info(localStorage)

      this.$rest
        .post("/v1/auth/login", { uid: uid, pass: password })
        .then(res => {
          if (res.status === 200) {
            let auth = res.data.auth_info;
            alert("로그인 성공");
            console.info(res.data);

            //this.$store.state.accessToken = auth.accessToken;

            //document.cookie = `accessToken=${auth.accessToken}`;
            //document.cookie = `refreshToken=${auth.refreshToken}`;
            //this.$rest.defaults.headers.common['x-access-token'] = auth.accessToken;

            this.$store.commit("auth_info", auth);
            this.$router.push({ name: "home" });
          }
        })
        .catch(err => {
          console.info(err);
          alert("로그인 실패");
        });
      /*
                axios.post('http://localhost:3000/auth/signin', { uid, password })
                    .then(res => {
                        if (res.status === 200) {
                            alert('로그인 성공');
                            document.cookie = `accessToken=${res.data.data.accessToken}`;
                            axios.defaults.headers.common['x-access-token'] = res.data.data.accessToken;
                            this.$router.push({ name: 'Home' });
                        }
                    })
                    .catch(err => {
                        alert('로그인 실패');
                    })
                    */
    }
  }
};
</script>

